import React, { useState, useEffect } from 'react';
import AuthWrapper from './components/AuthWrapper';
import { FiFilm, FiPlus, FiEye, FiEdit2, FiCopy, FiTrash2, FiList, FiGrid, FiLogOut, FiDownload, FiUpload } from 'react-icons/fi';
import ShotlistTable from './components/ShotlistTable';
import PDFExportModal from './components/PDFExportModal';

// Add these functions at the top of your file, outside of the App component

const dbName = 'DirectorsDeckDB';
const storeName = 'projects';

const openDB = () => {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(dbName, 1);
    request.onerror = () => reject(request.error);
    request.onsuccess = () => resolve(request.result);
    request.onupgradeneeded = (event) => {
      const db = event.target.result;
      db.createObjectStore(storeName, { keyPath: 'id' });
    };
  });
};

const getProjects = async () => {
  const db = await openDB();
  return new Promise((resolve, reject) => {
    const transaction = db.transaction(storeName, 'readonly');
    const store = transaction.objectStore(storeName);
    const request = store.getAll();
    request.onerror = () => reject(request.error);
    request.onsuccess = () => resolve(request.result);
  });
};

const saveProject = async (project) => {
  const db = await openDB();
  return new Promise((resolve, reject) => {
    const transaction = db.transaction(storeName, 'readwrite');
    const store = transaction.objectStore(storeName);
    const request = store.put(project);
    request.onerror = () => reject(request.error);
    request.onsuccess = () => resolve(request.result);
  });
};

const deleteProject = async (id) => {
  const db = await openDB();
  return new Promise((resolve, reject) => {
    const transaction = db.transaction(storeName, 'readwrite');
    const store = transaction.objectStore(storeName);
    const request = store.delete(id);
    request.onerror = () => reject(request.error);
    request.onsuccess = () => resolve(request.result);
  });
};

function App() {
  const [projects, setProjects] = useState([]);
  const [currentProject, setCurrentProject] = useState(null);
  const [showNewProjectModal, setShowNewProjectModal] = useState(false);
  const [showEditProjectModal, setShowEditProjectModal] = useState(false);
  const [editingProject, setEditingProject] = useState(null);
  const [newProject, setNewProject] = useState({
    name: '',
    director: '',
    dp: '',
    production: '',
    producer: '',
    date: new Date().toISOString().split('T')[0]
  });
  const [viewMode, setViewMode] = useState('grid');
  const [showPDFExportModal, setShowPDFExportModal] = useState(false);
  const [productionFilter, setProductionFilter] = useState('');
  const [uniqueProductions, setUniqueProductions] = useState([]);
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  const [projectName, setProjectName] = useState('');
  const [director, setDirector] = useState('');
  const [dp, setDP] = useState('');
  const [production, setProduction] = useState('');
  const [producer, setProducer] = useState('');

  useEffect(() => {
    loadProjects();
  }, []);

  const loadProjects = async () => {
    try {
      const projectsData = await getProjects();
      setProjects(projectsData);
      const productions = [...new Set(projectsData.map(p => p.production).filter(Boolean))];
      setUniqueProductions(productions);
    } catch (error) {
      console.error('Fehler beim Laden der Projekte:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleProjectSelect = async (id) => {
    try {
      const project = projects.find(p => p.id === id);
      setCurrentProject(project);
      setProjectName(project.name);
      setDirector(project.director);
      setDP(project.dp);
      setProduction(project.production);
      setProducer(project.producer);
    } catch (error) {
      console.error('Fehler beim Laden des Projekts:', error);
    }
  };

  const handleCreateProject = async () => {
    const project = {
      id: Date.now().toString(),
      ...newProject,
      shotlist: { scenes: [] },
      lastModified: new Date().toISOString(),
      production: newProject.production || ''
    };
    try {
      await saveProject(project);
      setProjects([...projects, project]);
      setNewProject({
        name: '',
        director: '',
        dp: '',
        production: '',
        producer: '',
        date: new Date().toISOString().split('T')[0]
      });
      setShowNewProjectModal(false);
    } catch (error) {
      console.error('Fehler beim Erstellen des Projekts:', error);
      alert('Fehler beim Erstellen des Projekts. Bitte versuchen Sie es erneut.');
    }
  };

  const handleEditProject = (project) => {
    setEditingProject(project);
    setShowEditProjectModal(true);
  };

  const handleUpdateProject = async () => {
    try {
      await saveProject(editingProject);
      setProjects(projects.map(p => p.id === editingProject.id ? editingProject : p));
      setShowEditProjectModal(false);
      setEditingProject(null);
    } catch (error) {
      console.error('Fehler beim Aktualisieren des Projekts:', error);
    }
  };

  const handleDeleteProject = async (id) => {
    if (window.confirm('Möchten Sie dieses Projekt wirklich löschen?')) {
      try {
        await deleteProject(id);
        setProjects(projects.filter(p => p.id !== id));
      } catch (error) {
        console.error('Fehler beim Löschen des Projekts:', error);
      }
    }
  };

  const handleDuplicateProject = async (project) => {
    const duplicatedProject = {
      ...project,
      id: Date.now().toString(),
      name: `${project.name} (Kopie)`,
      lastModified: new Date().toISOString()
    };
    try {
      await saveProject(duplicatedProject);
      setProjects([...projects, duplicatedProject]);
    } catch (error) {
      console.error('Fehler beim Duplizieren des Projekts:', error);
    }
  };

  const handleShotlistUpdate = async (newShotlist) => {
    if (currentProject) {
      const updatedProject = { ...currentProject, shotlist: newShotlist, lastModified: new Date().toISOString() };
      setCurrentProject(updatedProject);
      try {
        await saveProject(updatedProject);
        setProjects(projects.map(p => p.id === currentProject.id ? updatedProject : p));
      } catch (error) {
        console.error('Fehler beim Aktualisieren der Shotlist:', error);
      }
    }
  };

  const filteredProjects = projects.filter(project => 
    !productionFilter || (project.production && project.production === productionFilter)
  );

  const handleExportProject = async (project) => {
    const projectData = JSON.stringify(project);
    const blob = new Blob([projectData], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `${project.name}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleImportProject = async (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = async (e) => {
        try {
          const projectData = JSON.parse(e.target.result);
          // Generiere eine neue ID für das importierte Projekt
          projectData.id = Date.now().toString();
          await saveProject(projectData);
          setProjects([...projects, projectData]);
          alert('Projekt erfolgreich importiert!');
        } catch (error) {
          console.error('Fehler beim Importieren des Projekts:', error);
          alert('Fehler beim Importieren des Projekts. Bitte versuchen Sie es erneut.');
        }
      };
      reader.readAsText(file);
    }
  };

  const renderProjects = (projectList) => {
    if (viewMode === 'grid') {
      return (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
          {projectList.map(project => (
            <div key={project.id} className="bg-gray-800 rounded-lg overflow-hidden shadow-lg p-4">
              <h3 className="text-lg font-semibold mb-2">{project.name}</h3>
              <p className="text-gray-400 text-sm mb-1">Director: {project.director}</p>
              <p className="text-gray-400 text-sm mb-1">DP: {project.dp}</p>
              <p className="text-gray-400 text-sm mb-1">Produktion: {project.production}</p>
              <p className="text-gray-400 text-sm mb-2">
                Szenen: {project.shotlist.scenes.length} | 
                Shots: {project.shotlist.scenes.reduce((total, scene) => total + scene.shots.length, 0)}
              </p>
              <div className="flex justify-between items-center">
                <button 
                  onClick={() => handleProjectSelect(project.id)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiEye />
                </button>
                <button 
                  onClick={() => handleEditProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiEdit2 />
                </button>
                <button 
                  onClick={() => handleDuplicateProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiCopy />
                </button>
                <button 
                  onClick={() => handleDeleteProject(project.id)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiTrash2 />
                </button>
                <button 
                  onClick={() => handleExportProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiDownload />
                </button>
              </div>
            </div>
          ))}
        </div>
      );
    } else {
      return (
        <div className="space-y-2">
          {projectList.map(project => (
            <div key={project.id} className="bg-gray-800 rounded-lg overflow-hidden shadow-lg p-4 flex justify-between items-center">
              <div className="flex-grow flex items-center space-x-4">
                <span className="font-semibold">{project.name}</span>
                <span className="text-gray-400">Director: {project.director}</span>
                <span className="text-gray-400">DP: {project.dp}</span>
                <span className="text-gray-400">Produktion: {project.production}</span>
                <span className="text-gray-400">
                  Szenen: {project.shotlist.scenes.length} | 
                  Shots: {project.shotlist.scenes.reduce((total, scene) => total + scene.shots.length, 0)}
                </span>
              </div>
              <div className="flex items-center space-x-2">
                <button 
                  onClick={() => handleProjectSelect(project.id)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiEye />
                </button>
                <button 
                  onClick={() => handleEditProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiEdit2 />
                </button>
                <button 
                  onClick={() => handleDuplicateProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiCopy />
                </button>
                <button 
                  onClick={() => handleDeleteProject(project.id)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiTrash2 />
                </button>
                <button 
                  onClick={() => handleExportProject(project)}
                  className="text-gray-400 hover:text-gray-300 transition-colors"
                >
                  <FiDownload />
                </button>
              </div>
            </div>
          ))}
        </div>
      );
    }
  };

  const handleLogout = async () => {
    try {
      // No need to sign out from local storage
    } catch (error) {
      console.error('Fehler beim Ausloggen:', error);
    }
  };

  if (currentProject) {
    return (
      <div>
        <ShotlistTable 
          initialShotlist={currentProject.shotlist}
          updateShotlist={handleShotlistUpdate}
          onBack={() => setCurrentProject(null)}
          projectName={projectName}
          director={director}
          dp={dp}
          production={production}
          producer={producer}
        />
        <button onClick={() => setShowPDFExportModal(true)}>Export PDF</button>
        {showPDFExportModal && (
          <PDFExportModal
            shotlist={currentProject.shotlist}
            projectName={projectName}
            director={director}
            dp={dp}
            production={production}
            producer={producer}
            onClose={() => setShowPDFExportModal(false)}
          />
        )}
      </div>
    );
  }

  return (
    <AuthWrapper>
      <div className="min-h-screen bg-gray-900 text-gray-100">
        <header className="bg-gray-800 py-4 px-6 flex justify-between items-center">
          <h1 className="text-2xl font-bold flex items-center">
            <FiFilm className="mr-2 text-yellow-400" /> Directors Deck
          </h1>
          <button
            onClick={handleLogout}
            className="bg-red-500 hover:bg-red-600 text-white px-4 py-2 rounded flex items-center"
          >
            <FiLogOut className="mr-2" /> Ausloggen
          </button>
        </header>
        <main className="container mx-auto px-6 py-8">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold">Projekte</h2>
            <div className="flex space-x-2">
              <button
                onClick={() => setShowNewProjectModal(true)}
                className="bg-yellow-400 text-gray-900 px-4 py-2 rounded hover:bg-yellow-300 transition-colors flex items-center"
              >
                <FiPlus className="mr-2" /> Neues Projekt
              </button>
              <label className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-400 transition-colors flex items-center cursor-pointer">
                <FiUpload className="mr-2" /> Projekt importieren
                <input
                  type="file"
                  accept=".json"
                  onChange={handleImportProject}
                  className="hidden"
                />
              </label>
            </div>
          </div>
          <div className="mb-4">
            <select
              value={productionFilter}
              onChange={(e) => setProductionFilter(e.target.value)}
              className="w-full p-2 bg-gray-700 rounded"
            >
              <option value="">Alle Produktionen</option>
              {uniqueProductions.map(production => (
                <option key={production} value={production}>{production}</option>
              ))}
            </select>
          </div>
          {isLoading ? (
            <p>Projekte werden geladen...</p>
          ) : projects.length === 0 ? (
            <p>Keine Projekte gefunden. Erstellen Sie ein neues Projekt, um loszulegen!</p>
          ) : (
            renderProjects(filteredProjects)
          )}
        </main>
        {showNewProjectModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
            <div className="bg-gray-800 p-8 rounded-lg shadow-xl w-96">
              <h2 className="text-2xl font-bold mb-4">Neues Projekt erstellen</h2>
              <input
                type="text"
                value={newProject.name}
                onChange={(e) => setNewProject({...newProject, name: e.target.value})}
                placeholder="Projektname"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="date"
                value={newProject.date}
                onChange={(e) => setNewProject({...newProject, date: e.target.value})}
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={newProject.director}
                onChange={(e) => setNewProject({...newProject, director: e.target.value})}
                placeholder="Director"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={newProject.dp}
                onChange={(e) => setNewProject({...newProject, dp: e.target.value})}
                placeholder="DP"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={newProject.production}
                onChange={(e) => setNewProject({...newProject, production: e.target.value})}
                placeholder="Produktion"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={newProject.producer}
                onChange={(e) => setNewProject({...newProject, producer: e.target.value})}
                placeholder="Producer"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <div className="flex justify-end">
                <button 
                  onClick={() => {
                    setShowNewProjectModal(false);
                    console.log('Modal sollte jetzt geschlossen sein');
                  }}
                  className="mr-2 px-4 py-2 border border-gray-600 rounded hover:bg-gray-700 transition-colors"
                >
                  Abbrechen
                </button>
                <button 
                  onClick={handleCreateProject}
                  className="px-4 py-2 bg-yellow-400 text-gray-900 rounded hover:bg-yellow-300 transition-colors"
                >
                  Erstellen
                </button>
              </div>
            </div>
          </div>
        )}
        {showEditProjectModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
            <div className="bg-gray-800 p-8 rounded-lg shadow-xl w-96">
              <h2 className="text-2xl font-bold mb-4">Projekt bearbeiten</h2>
              <input
                type="text"
                value={editingProject.name}
                onChange={(e) => setEditingProject({...editingProject, name: e.target.value})}
                placeholder="Projektname"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="date"
                value={editingProject.date}
                onChange={(e) => setEditingProject({...editingProject, date: e.target.value})}
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={editingProject.director}
                onChange={(e) => setEditingProject({...editingProject, director: e.target.value})}
                placeholder="Director"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={editingProject.dp}
                onChange={(e) => setEditingProject({...editingProject, dp: e.target.value})}
                placeholder="DP"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={editingProject.production}
                onChange={(e) => setEditingProject({...editingProject, production: e.target.value})}
                placeholder="Produktion"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <input
                type="text"
                value={editingProject.producer}
                onChange={(e) => setEditingProject({...editingProject, producer: e.target.value})}
                placeholder="Producer"
                className="w-full p-2 bg-gray-700 rounded mb-4"
              />
              <div className="flex justify-end">
                <button 
                  onClick={() => setShowEditProjectModal(false)}
                  className="mr-2 px-4 py-2 border border-gray-600 rounded hover:bg-gray-700 transition-colors"
                >
                  Abbrechen
                </button>
                <button 
                  onClick={handleUpdateProject}
                  className="px-4 py-2 bg-yellow-400 text-gray-900 rounded hover:bg-yellow-300 transition-colors"
                >
                  Aktualisieren
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </AuthWrapper>
  );
}

export default App;