import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import jsPDF from 'jspdf';
import 'jspdf-autotable';

const colorScheme = {
  background: [255, 255, 255],
  primary: [66, 153, 225],
  secondary: [160, 174, 192],
  text: [45, 55, 72]
};

const generateListWithImagesPDF = (doc, shotlist, coverInfo) => {
  const pageWidth = 210;
  const pageHeight = 297;
  const margin = 10;
  const contentWidth = pageWidth - (2 * margin);

  // Cover page
  const drawCoverPage = () => {
    // Background
    doc.setFillColor(...colorScheme.background);
    doc.rect(0, 0, pageWidth, pageHeight, 'F');

    // Large, bold project name
    doc.setTextColor(...colorScheme.primary);
    doc.setFont("helvetica", "bold");
    doc.setFontSize(40);
    const projectName = coverInfo.projectName || 'Untitled Project';
    doc.text(projectName, pageWidth / 2, 60, { align: 'center', maxWidth: pageWidth - (2 * margin) });

    // Subtle line under project name
    doc.setDrawColor(...colorScheme.primary);
    doc.setLineWidth(0.5);
    doc.line(margin, 70, pageWidth - margin, 70);

    // Project info in the middle and smaller
    const infoFields = [
      { label: 'Director', value: coverInfo.director },
      { label: 'DP', value: coverInfo.dp },
      { label: 'Production', value: coverInfo.production },
      { label: 'Date', value: new Date().toLocaleDateString() }
    ];

    let yPos = pageHeight / 2 - 30; // Start from the middle of the page
    infoFields.forEach(field => {
      // Label
      doc.setFontSize(10);
      doc.setTextColor(...colorScheme.secondary);
      doc.setFont("helvetica", "normal");
      doc.text(field.label, pageWidth / 2, yPos, { align: 'center' });

      // Value
      doc.setFontSize(12);
      doc.setTextColor(...colorScheme.text);
      doc.setFont("helvetica", "bold");
      doc.text(field.value || 'N/A', pageWidth / 2, yPos + 6, { align: 'center' });

      yPos += 20;
    });

    // "Shotlist" text at the bottom
    doc.setTextColor(...colorScheme.primary);
    doc.setFont("helvetica", "bold");
    doc.setFontSize(14);
    doc.text("SHOTLIST", pageWidth / 2, pageHeight - 20, { align: 'center' });
  };

  // Footer
  const drawFooter = (pageNumber, totalPages) => {
    doc.setFillColor(...colorScheme.primary);
    doc.rect(0, pageHeight - 10, pageWidth, 10, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(8);
    doc.text(`Page ${pageNumber} of ${totalPages}`, pageWidth / 2, pageHeight - 3, { align: 'center' });
  };

  let yOffset = 10;

  const drawShot = (scene, shot) => {
    const shotHeight = 65;
    // Shot container (now white background)
    doc.setFillColor(255, 255, 255);
    doc.rect(margin, yOffset, contentWidth, shotHeight, 'F');

    // Shot number and title
    doc.setFillColor(...colorScheme.primary);
    doc.roundedRect(margin, yOffset, contentWidth, 8, 2, 2, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(9);
    doc.text(`${scene.number}.${shot.number} ${shot.title || ''}`, margin + 3, yOffset + 5.5);

    // Image (16:9 aspect ratio)
    const imageWidth = 60;
    const imageHeight = (imageWidth * 9) / 16;
    if (shot.image) {
      doc.addImage(shot.image, 'JPEG', margin + 3, yOffset + 10, imageWidth, imageHeight, null, 'FAST', 0);
    } else {
      doc.setFillColor(...colorScheme.background);
      doc.rect(margin + 3, yOffset + 10, imageWidth, imageHeight, 'F');
      doc.setTextColor(...colorScheme.text);
      doc.setFontSize(8);
      doc.text('No image', margin + 3 + imageWidth / 2, yOffset + 10 + imageHeight / 2, { align: 'center', baseline: 'middle' });
    }

    // Shot details
    const leftColumnX = margin + imageWidth + 8;
    const rightColumnX = pageWidth / 2 + 5;
    let detailsY = yOffset + 12;

    // Description (not truncated)
    doc.setFontSize(7);
    doc.setTextColor(...colorScheme.primary);
    doc.text('Description', leftColumnX, detailsY);
    doc.setFontSize(8);
    doc.setTextColor(...colorScheme.text);
    const descriptionLines = doc.splitTextToSize(shot.description || 'N/A', contentWidth - imageWidth - 10);
    doc.text(descriptionLines, leftColumnX, detailsY + 3);

    detailsY += 4 + (descriptionLines.length * 3) + 3; // Added extra space after description

    const fields = [
      { label: 'Camera Angle', value: shot.cameraAngle },
      { label: 'Camera Move', value: shot.cameraMove },
      { label: 'Lens', value: shot.lens },
      { label: 'Duration', value: shot.duration },
      { label: 'Location', value: shot.location },
      { label: 'Props', value: shot.props },
      { label: 'Cast', value: shot.cast }
    ];

    fields.forEach((field, index) => {
      const columnX = index % 2 === 0 ? leftColumnX : rightColumnX;
      doc.setFontSize(7);
      doc.setTextColor(...colorScheme.primary);
      doc.text(field.label, columnX, detailsY);
      doc.setFontSize(8);
      doc.setTextColor(...colorScheme.text);
      const valueLines = doc.splitTextToSize(field.value || 'N/A', 45);
      doc.text(valueLines, columnX, detailsY + 3);
      
      if (index % 2 === 1 || index === fields.length - 1) {
        detailsY += 4 + (Math.max(valueLines.length, doc.splitTextToSize(fields[index-1]?.value || 'N/A', 45).length) * 3);
      }
    });

    yOffset += detailsY - yOffset + 10;
  };

  // Draw cover page
  drawCoverPage();
  doc.addPage();

  shotlist.scenes.forEach((scene, sceneIndex) => {
    if (yOffset > pageHeight - 75) {
      doc.addPage();
      yOffset = 10;
    }

    // Scene header (only scene name) with darker background
    const darkerPrimary = colorScheme.primary.map(c => Math.max(0, c - 30)); // Darken the primary color
    doc.setFillColor(...darkerPrimary);
    doc.rect(0, yOffset, pageWidth, 10, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(11);
    doc.text(scene.name || `Unnamed Scene ${scene.number}`, margin, yOffset + 7);
    yOffset += 15;

    scene.shots.forEach((shot) => {
      if (yOffset > pageHeight - 75) {
        doc.addPage();
        yOffset = 10;
      }
      drawShot(scene, shot);
    });
  });

  // Add footer to all pages except the cover
  const totalPages = doc.internal.getNumberOfPages();
  for (let i = 2; i <= totalPages; i++) {
    doc.setPage(i);
    drawFooter(i - 1, totalPages - 1);
  }
};

const generateListWithoutImagesPDF = (doc, shotlist, coverInfo) => {
  const pageWidth = 210;
  const pageHeight = 297;
  const margin = 10;

  // Cover page
  drawCoverPage(doc, coverInfo, pageWidth, pageHeight, margin, colorScheme);
  doc.addPage();

  shotlist.scenes.forEach((scene) => {
    // Scene header
    doc.setFillColor(...colorScheme.primary);
    doc.rect(0, 10, pageWidth, 10, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(12);
    doc.text(`Scene ${scene.number}: ${scene.name || 'Unnamed Scene'}`, margin, 17);

    // Prepare data for the table
    const tableData = scene.shots.map(shot => [
      `${scene.number}.${shot.number}`,
      shot.title || '',
      shot.description || '',
      shot.cameraAngle || '',
      shot.cameraMove || '',
      shot.lens || '',
      shot.duration || '',
      shot.location || '',
      shot.props || '',
      shot.cast || ''
    ]);

    // Generate the table
    doc.autoTable({
      startY: 25,
      head: [['Shot', 'Title', 'Description', 'Camera Angle', 'Camera Move', 'Lens', 'Duration', 'Location', 'Props', 'Cast']],
      body: tableData,
      theme: 'grid',
      styles: { fontSize: 8, cellPadding: 1 },
      headStyles: { fillColor: colorScheme.primary, textColor: [255, 255, 255] },
      columnStyles: {
        0: { cellWidth: 10 },
        1: { cellWidth: 20 },
        2: { cellWidth: 40 },
      },
      didDrawPage: (data) => {
        // Add footer to each page
        drawFooter(doc, doc.internal.getCurrentPageInfo().pageNumber, doc.internal.getNumberOfPages(), pageWidth, pageHeight, colorScheme);
      }
    });

    // Add a new page after each scene, except for the last one
    if (scene !== shotlist.scenes[shotlist.scenes.length - 1]) {
      doc.addPage();
    }
  });
};

const generateStoryboardPDF = (doc, shotlist, coverInfo) => {
  const pageWidth = 210;
  const pageHeight = 297;
  const margin = 10;
  const contentWidth = (pageWidth - (3 * margin)) / 2; // Breite für zwei Spalten
  const shotHeight = 140; // Höhe für jeden Shot

  // Cover page
  drawCoverPage(doc, coverInfo, pageWidth, pageHeight, margin, colorScheme);
  doc.addPage();

  let yOffset = margin;
  let xOffset = margin;
  let shotCount = 0;

  shotlist.scenes.forEach((scene, sceneIndex) => {
    if (sceneIndex > 0 && shotCount !== 0) {
      // Neue Seite für jede neue Szene, außer es ist die erste Szene oder der Anfang einer neuen Seite
      doc.addPage();
      yOffset = margin;
      xOffset = margin;
      shotCount = 0;
    }

    // Szenen-Header
    doc.setFillColor(...colorScheme.primary);
    doc.rect(margin, yOffset, pageWidth - 2 * margin, 10, 'F');
    doc.setTextColor(255, 255, 255);
    doc.setFontSize(12);
    doc.setFont("helvetica", "bold");
    doc.text(`Scene ${scene.number}: ${scene.name || 'Unnamed Scene'}`, margin + 5, yOffset + 7);
    yOffset += 15;

    scene.shots.forEach((shot) => {
      if (shotCount === 4) {
        doc.addPage();
        yOffset = margin;
        xOffset = margin;
        shotCount = 0;
      }

      // Shot container
      doc.setFillColor(245, 245, 245);
      doc.rect(xOffset, yOffset, contentWidth, shotHeight, 'F');

      // Shot title
      doc.setTextColor(...colorScheme.text);
      doc.setFontSize(10);
      doc.setFont("helvetica", "bold");
      doc.text(`${scene.number}.${shot.number} ${shot.title || ''}`, xOffset + 3, yOffset + 7);

      // Image (16:9 aspect ratio)
      const imageWidth = contentWidth - 6;
      const imageHeight = (imageWidth * 9) / 16;
      if (shot.image) {
        doc.addImage(shot.image, 'JPEG', xOffset + 3, yOffset + 12, imageWidth, imageHeight, null, 'FAST', 0);
      } else {
        doc.setFillColor(200, 200, 200);
        doc.rect(xOffset + 3, yOffset + 12, imageWidth, imageHeight, 'F');
        doc.setTextColor(100, 100, 100);
        doc.setFontSize(8);
        doc.text('No image', xOffset + 3 + imageWidth / 2, yOffset + 12 + imageHeight / 2, { align: 'center', baseline: 'middle' });
      }

      // Shot details
      let detailsY = yOffset + 15 + imageHeight;
      doc.setFontSize(8);
      doc.setTextColor(...colorScheme.text);
      doc.setFont("helvetica", "normal");

      const fields = [
        { label: 'Description', value: shot.description },
        { label: 'Camera Angle', value: shot.cameraAngle },
        { label: 'Camera Move', value: shot.cameraMove },
        { label: 'Lens', value: shot.lens },
        { label: 'Duration', value: shot.duration },
        { label: 'Location', value: shot.location },
        { label: 'Props', value: shot.props },
        { label: 'Cast', value: shot.cast }
      ];

      // Description (full width)
      if (fields[0].value) {
        doc.setFont("helvetica", "bold");
        doc.text(`${fields[0].label}:`, xOffset + 5, detailsY);
        doc.setFont("helvetica", "normal");
        const lines = doc.splitTextToSize(fields[0].value, contentWidth - 10);
        doc.text(lines, xOffset + 5, detailsY + 3);
        detailsY += 3 + (lines.length * 3) + 3; // Extra space after description
      }

      // Other fields (two columns)
      const columnWidth = (contentWidth - 15) / 2;
      let leftColumnY = detailsY;
      let rightColumnY = detailsY;

      for (let i = 1; i < fields.length; i++) {
        if (fields[i].value) {
          const isLeftColumn = i % 2 === 1;
          const columnX = isLeftColumn ? xOffset + 5 : xOffset + columnWidth + 10;
          const columnY = isLeftColumn ? leftColumnY : rightColumnY;

          doc.setFont("helvetica", "bold");
          doc.text(`${fields[i].label}:`, columnX, columnY);
          doc.setFont("helvetica", "normal");
          const lines = doc.splitTextToSize(fields[i].value, columnWidth - 5);
          doc.text(lines, columnX, columnY + 3);

          if (isLeftColumn) {
            leftColumnY += 3 + (lines.length * 3) + 3;
          } else {
            rightColumnY += 3 + (lines.length * 3) + 3;
            detailsY = Math.max(leftColumnY, rightColumnY);
          }
        }
      }

      shotCount++;
      if (shotCount % 2 === 0) {
        xOffset = margin;
        yOffset += shotHeight + 10; // Erhöhter Abstand zwischen den Zeilen
      } else {
        xOffset += contentWidth + margin;
      }
    });
  });

  // Add footer to all pages except the cover
  const totalPages = doc.internal.getNumberOfPages();
  for (let i = 2; i <= totalPages; i++) {
    doc.setPage(i);
    drawFooter(doc, i - 1, totalPages - 1, pageWidth, pageHeight, colorScheme);
  }
};

const drawCoverPage = (doc, coverInfo, pageWidth, pageHeight, margin, colorScheme) => {
  // Background
  doc.setFillColor(...colorScheme.background);
  doc.rect(0, 0, pageWidth, pageHeight, 'F');

  // Large, bold project name
  doc.setTextColor(...colorScheme.primary);
  doc.setFont("helvetica", "bold");
  doc.setFontSize(40);
  const projectName = coverInfo.projectName || 'Untitled Project';
  doc.text(projectName, pageWidth / 2, 60, { align: 'center', maxWidth: pageWidth - (2 * margin) });

  // Subtle line under project name
  doc.setDrawColor(...colorScheme.primary);
  doc.setLineWidth(0.5);
  doc.line(margin, 70, pageWidth - margin, 70);

  // Project info in the middle and smaller
  const infoFields = [
    { label: 'Director', value: coverInfo.director },
    { label: 'DP', value: coverInfo.dp },
    { label: 'Production', value: coverInfo.production },
    { label: 'Date', value: new Date().toLocaleDateString() }
  ];

  let yPos = pageHeight / 2 - 30; // Start from the middle of the page
  infoFields.forEach(field => {
    // Label
    doc.setFontSize(10);
    doc.setTextColor(...colorScheme.secondary);
    doc.setFont("helvetica", "normal");
    doc.text(field.label, pageWidth / 2, yPos, { align: 'center' });

    // Value
    doc.setFontSize(12);
    doc.setTextColor(...colorScheme.text);
    doc.setFont("helvetica", "bold");
    doc.text(field.value || 'N/A', pageWidth / 2, yPos + 6, { align: 'center' });

    yPos += 20;
  });

  // "Shotlist" text at the bottom
  doc.setTextColor(...colorScheme.primary);
  doc.setFont("helvetica", "bold");
  doc.setFontSize(14);
  doc.text("SHOTLIST", pageWidth / 2, pageHeight - 20, { align: 'center' });
};

const drawFooter = (doc, pageNumber, totalPages, pageWidth, pageHeight, colorScheme) => {
  doc.setFillColor(...colorScheme.primary);
  doc.rect(0, pageHeight - 10, pageWidth, 10, 'F');
  doc.setTextColor(255, 255, 255);
  doc.setFontSize(8);
  doc.text(`Page ${pageNumber} of ${totalPages}`, pageWidth / 2, pageHeight - 3, { align: 'center' });
};

const PDFExportModal = ({ 
  shotlist, 
  projectInfo, 
  onClose,
  cardBgColor,
  textColor,
  inputBgColor,
  buttonBgColor,
  buttonTextColor
}) => {
  const [exportMode, setExportMode] = useState('listWithImages');
  const [coverInfo, setCoverInfo] = useState({
    projectName: projectInfo?.projectName || '',
    director: '',
    dp: '',
    production: ''
  });

  const handleCoverInfoChange = (e) => {
    setCoverInfo({ ...coverInfo, [e.target.name]: e.target.value });
  };

  const handleExport = () => {
    if (!shotlist || !Array.isArray(shotlist.scenes)) {
      console.error('Invalid shotlist data');
      alert('Fehler beim Exportieren. Ungültige Shotlist-Daten.');
      return;
    }

    const doc = new jsPDF();
    switch (exportMode) {
      case 'listWithImages':
        generateListWithImagesPDF(doc, shotlist, coverInfo);
        break;
      case 'listWithoutImages':
        generateListWithoutImagesPDF(doc, shotlist, coverInfo);
        break;
      case 'storyboard':
        generateStoryboardPDF(doc, shotlist, coverInfo);
        break;
      default:
        console.error('Invalid export mode');
        return;
    }
    doc.save(`${coverInfo.projectName || 'shotlist'}.pdf`);
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className={`${cardBgColor} p-6 rounded-lg max-w-2xl w-full`}>
        <div className="flex justify-between items-center mb-6">
          <h2 className={`text-2xl font-bold ${textColor}`}>Export PDF</h2>
          <button onClick={onClose} className={`${textColor} hover:text-gray-400`}>
            <FiX size={24} />
          </button>
        </div>
        
        <div className="grid grid-cols-2 gap-4 mb-6">
          <div>
            <label className={`block mb-2 ${textColor}`}>Projektname:</label>
            <input
              type="text"
              name="projectName"
              value={coverInfo.projectName}
              onChange={handleCoverInfoChange}
              className={`w-full ${inputBgColor} ${textColor} px-3 py-2 rounded`}
            />
          </div>
          
          <div>
            <label className={`block mb-2 ${textColor}`}>Regisseur:</label>
            <input
              type="text"
              name="director"
              value={coverInfo.director}
              onChange={handleCoverInfoChange}
              className={`w-full ${inputBgColor} ${textColor} px-3 py-2 rounded`}
            />
          </div>
          
          <div>
            <label className={`block mb-2 ${textColor}`}>DP:</label>
            <input
              type="text"
              name="dp"
              value={coverInfo.dp}
              onChange={handleCoverInfoChange}
              className={`w-full ${inputBgColor} ${textColor} px-3 py-2 rounded`}
            />
          </div>
          
          <div>
            <label className={`block mb-2 ${textColor}`}>Produktion:</label>
            <input
              type="text"
              name="production"
              value={coverInfo.production}
              onChange={handleCoverInfoChange}
              className={`w-full ${inputBgColor} ${textColor} px-3 py-2 rounded`}
            />
          </div>
        </div>

        <div className="mb-6">
          <label className={`block mb-2 ${textColor}`}>Exportformat:</label>
          <select
            value={exportMode}
            onChange={(e) => setExportMode(e.target.value)}
            className={`w-full ${inputBgColor} ${textColor} px-3 py-2 rounded`}
          >
            <option value="listWithImages">Liste mit Bildern</option>
            <option value="listWithoutImages">Liste ohne Bilder</option>
            <option value="storyboard">Storyboard</option>
          </select>
        </div>
        
        <div className="flex justify-end">
          <button onClick={onClose} className={`${buttonBgColor} ${buttonTextColor} px-4 py-2 rounded mr-2`}>
            Abbrechen
          </button>
          <button onClick={handleExport} className={`${buttonBgColor} ${buttonTextColor} px-4 py-2 rounded`}>
            Exportieren
          </button>
        </div>
      </div>
    </div>
  );
};

export default PDFExportModal;