import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { FiTrash2, FiUpload, FiSettings, FiPlus, FiDownload, FiFilm, FiEdit2, FiChevronLeft, FiChevronRight, FiArrowLeft, FiImage, FiList, FiGrid, FiCopy, FiPrinter, FiCamera, FiUser, FiCalendar, FiAlignLeft, FiMenu } from 'react-icons/fi';
import PDFExportModal from './PDFExportModal';

const shotTypes = [
  'Establishing Shot', 'Wide Shot', 'Medium Shot', 'Close-Up', 'Extreme Close-Up',
  'Over-the-Shoulder', 'Point of View', 'Two Shot', 'Insert Shot', 'Cutaway',
];

const cameraAngles = ['High Angle', 'Low Angle', 'Eye Level', 'Dutch Angle', 'Bird\'s Eye View', 'Worm\'s Eye View'];
const cameraMoves = ['Static', 'Pan', 'Tilt', 'Zoom', 'Dolly', 'Truck', 'Pedestal', 'Crane'];
const lensTypes = ['Wide Angle', 'Standard', 'Telephoto', 'Macro', 'Fish Eye', 'Prime', 'Zoom'];

const ShotlistTable = ({ 
  initialShotlist = { scenes: [] }, 
  updateShotlist, 
  onBack, 
  projectName,
  director,
  dp,
  production,
  producer,
  camOp,
  shootingDate
}) => {
  const [shotlist, setShotlist] = useState(initialShotlist);
  const [selectedSceneId, setSelectedSceneId] = useState(null);
  const [visibleFields, setVisibleFields] = useState([
    'description', 'shotType', 'cameraAngle', 'cameraMove', 'lens', 'duration', 'props', 'location', 'cast'
  ]);
  const [showSettings, setShowSettings] = useState(false);
  const [showPDFExportModal, setShowPDFExportModal] = useState(false);
  const [viewMode, setViewMode] = useState('default');
  const [isSceneMenuVisible, setIsSceneMenuVisible] = useState(true);

  useEffect(() => {
    setShotlist(initialShotlist);
    if (initialShotlist.scenes && initialShotlist.scenes.length > 0 && !selectedSceneId) {
      setSelectedSceneId(initialShotlist.scenes[0].id);
    }
  }, [initialShotlist, selectedSceneId]);

  const handleAddNewShot = useCallback(() => {
    if (selectedSceneId) {
      setShotlist(prevShotlist => {
        const updatedScenes = prevShotlist.scenes.map(scene => {
          if (scene.id === selectedSceneId) {
            const newShot = {
              id: Date.now().toString(),
              image: null,
              shotType: '',
              description: '',
              cameraAngle: '',
              cameraMove: '',
              lens: '',
              number: scene.shots.length + 1,
              duration: ''
            };
            return { ...scene, shots: [...scene.shots, newShot] };
          }
          return scene;
        });
        const updatedShotlist = { ...prevShotlist, scenes: updatedScenes };
        updateShotlist(updatedShotlist);
        return updatedShotlist;
      });
    }
  }, [selectedSceneId, updateShotlist]);

  const handleUpdateShot = useCallback((sceneId, shotId, updatedShot) => {
    setShotlist(prevShotlist => {
      const updatedShotlist = {
        ...prevShotlist,
        scenes: prevShotlist.scenes.map(scene =>
          scene.id === sceneId
            ? {
                ...scene,
                shots: scene.shots.map(shot =>
                  shot.id === shotId ? { ...shot, ...updatedShot } : shot
                ),
              }
            : scene
        ),
      };
      updateShotlist(updatedShotlist);
      return updatedShotlist;
    });
  }, [updateShotlist]);

  const handleDeleteShot = useCallback((sceneId, shotId) => {
    setShotlist(prevShotlist => {
      const updatedShotlist = {
        ...prevShotlist,
        scenes: prevShotlist.scenes.map(scene =>
          scene.id === sceneId
            ? { 
                ...scene, 
                shots: scene.shots.filter(shot => shot.id !== shotId)
                                  .map((shot, index) => ({ ...shot, number: index + 1 }))
              }
            : scene
        ),
      };
      updateShotlist(updatedShotlist);
      return updatedShotlist;
    });
  }, [updateShotlist]);

  const handleImageUpload = useCallback((sceneId, shotId, file) => {
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const aspectRatio = 16 / 9;
          let newWidth, newHeight;

          if (img.width / img.height > aspectRatio) {
            newHeight = img.height;
            newWidth = img.height * aspectRatio;
          } else {
            newWidth = img.width;
            newHeight = img.width / aspectRatio;
          }

          canvas.width = newWidth;
          canvas.height = newHeight;

          ctx.drawImage(img, (img.width - newWidth) / 2, (img.height - newHeight) / 2, newWidth, newHeight, 0, 0, newWidth, newHeight);

          const scaledImageData = canvas.toDataURL('image/jpeg');
          
          setShotlist(prevShotlist => {
            const updatedScenes = prevShotlist.scenes.map(scene => {
              if (scene.id === sceneId) {
                const updatedShots = scene.shots.map(shot => {
                  if (shot.id === shotId) {
                    return { ...shot, image: scaledImageData };
                  }
                  return shot;
                });
                return { ...scene, shots: updatedShots };
              }
              return scene;
            });
            return { ...prevShotlist, scenes: updatedScenes };
          });
        };
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  }, []);

  const onDragEnd = useCallback((result) => {
    const { destination, source, draggableId, type } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    if (type === 'scene') {
      const newScenes = Array.from(shotlist.scenes);
      const [reorderedScene] = newScenes.splice(source.index, 1);
      newScenes.splice(destination.index, 0, reorderedScene);

      const updatedScenes = newScenes.map((scene, index) => ({
        ...scene,
        number: index + 1,
        shots: scene.shots.map(shot => ({
          ...shot,
          sceneNumber: index + 1
        }))
      }));

      const newShotlist = {
        ...shotlist,
        scenes: updatedScenes,
      };

      updateShotlist(newShotlist);
      setShotlist(newShotlist);
    } else if (type === 'shot') {
      const selectedScene = shotlist.scenes.find(scene => scene.id === selectedSceneId);
      if (!selectedScene) return;

      const newShots = Array.from(selectedScene.shots);
      const [reorderedShot] = newShots.splice(source.index, 1);
      newShots.splice(destination.index, 0, reorderedShot);

      const updatedScene = {
        ...selectedScene,
        shots: newShots.map((shot, index) => ({ ...shot, number: index + 1 })),
      };

      setShotlist(prevShotlist => ({
        ...prevShotlist,
        scenes: prevShotlist.scenes.map(scene =>
          scene.id === selectedScene.id ? updatedScene : scene
        ),
      }));
    }
  }, [shotlist, updateShotlist, selectedSceneId]);

  const handleSceneSelect = useCallback((sceneId) => {
    setSelectedSceneId(sceneId);
  }, []);

  const handleDeleteScene = useCallback((sceneId) => {
    const newScenes = shotlist.scenes.filter(scene => scene.id !== sceneId);
    const updatedScenes = newScenes.map((scene, index) => ({
      ...scene,
      number: index + 1,
      shots: scene.shots.map(shot => ({
        ...shot,
        sceneNumber: index + 1
      }))
    }));
    updateShotlist({ ...shotlist, scenes: updatedScenes });
    if (selectedSceneId === sceneId) {
      setSelectedSceneId(null);
    }
  }, [selectedSceneId, updateShotlist, shotlist.scenes]);

  const handleAddNewScene = useCallback(() => {
    const newScene = {
      id: Date.now().toString(),
      name: `Neue Szene ${shotlist.scenes.length + 1}`,
      number: shotlist.scenes.length + 1,
      shots: []
    };
    setShotlist(prevShotlist => {
      const updatedShotlist = {
        ...prevShotlist,
        scenes: [...prevShotlist.scenes, newScene]
      };
      updateShotlist(updatedShotlist);
      return updatedShotlist;
    });
  }, [shotlist, updateShotlist]);

  const selectedScene = useMemo(() => 
    shotlist.scenes.find(scene => scene.id === selectedSceneId),
    [shotlist.scenes, selectedSceneId]
  );

  const handleExportPDF = useCallback(() => {
    setShowPDFExportModal(true);
  }, []);

  const handleSceneNameChange = useCallback((sceneId, newName) => {
    setShotlist(prevShotlist => {
      const updatedShotlist = {
        ...prevShotlist,
        scenes: prevShotlist.scenes.map(scene =>
          scene.id === sceneId ? { ...scene, name: newName } : scene
        ),
      };
      updateShotlist(updatedShotlist);
      return updatedShotlist;
    });
  }, [updateShotlist]);

  const bgColor = "bg-gray-900";
  const textColor = "text-gray-200";
  const accentColor = "text-yellow-500";
  const buttonBgColor = "bg-yellow-500";
  const buttonTextColor = "text-gray-900";
  const hoverButtonBgColor = "hover:bg-yellow-600";
  const cardBgColor = "bg-gray-800";
  const inputBgColor = "bg-gray-700";
  const headerBgColor = "bg-gray-950";

  const toggleViewMode = () => {
    setViewMode(current => {
      switch (current) {
        case 'default': return 'storyboard';
        case 'storyboard': return 'list';
        case 'list': return 'default';
      }
    });
  };

  const getViewModeIcon = () => {
    switch (viewMode) {
      case 'default': return <FiList />;
      case 'storyboard': return <FiGrid />;
      case 'list': return <FiAlignLeft />;
    }
  };

  const renderShotNumber = (sceneNumber, shotNumber) => {
    return `${sceneNumber}.${shotNumber}`;
  };

  const handleShotTitleChange = (sceneId, shotId, newTitle) => {
    const updatedScenes = shotlist.scenes.map(scene => {
      if (scene.id === sceneId) {
        const updatedShots = scene.shots.map(shot => {
          if (shot.id === shotId) {
            return { ...shot, title: newTitle };
          }
          return shot;
        });
        return { ...scene, shots: updatedShots };
      }
      return scene;
    });
    updateShotlist({ ...shotlist, scenes: updatedScenes });
  };

  const calculateTotalDuration = (scenes) => {
    const totalMinutes = scenes.reduce((total, scene) => {
      return total + scene.shots.reduce((sceneTotal, shot) => {
        return shot.duration ? sceneTotal + parseInt(shot.duration, 10) : sceneTotal;
      }, 0);
    }, 0);
    
    if (totalMinutes >= 60) {
      const hours = Math.floor(totalMinutes / 60);
      const minutes = totalMinutes % 60;
      return `${hours}:${minutes.toString().padStart(2, '0')}h`;
    }
    return `${totalMinutes} min`;
  };

  const handleDuplicateShot = useCallback((sceneId, shotToDuplicate) => {
    setShotlist(prevShotlist => {
      const updatedScenes = prevShotlist.scenes.map(scene => {
        if (scene.id === sceneId) {
          const newShot = {
            ...shotToDuplicate,
            id: Date.now().toString(),
            number: scene.shots.length + 1
          };
          return { ...scene, shots: [...scene.shots, newShot] };
        }
        return scene;
      });
      const updatedShotlist = { ...prevShotlist, scenes: updatedScenes };
      updateShotlist(updatedShotlist);
      return updatedShotlist;
    });
  }, [updateShotlist]);

  const toggleSceneMenu = () => {
    setIsSceneMenuVisible(!isSceneMenuVisible);
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <div className={`flex flex-col h-screen ${bgColor} ${textColor}`}>
        <header className={`${headerBgColor} p-4 flex items-center justify-between`}>
          <div className="flex items-center">
            <button
              onClick={onBack}
              className={`mr-4 ${textColor} hover:${accentColor} transition-colors`}
            >
              <FiArrowLeft size={24} />
            </button>
            <FiFilm size={24} className={`mr-2 ${accentColor}`} />
            <span className={`text-xl font-bold ${textColor}`}>Directors Deck</span>
          </div>
          <h1 className={`text-2xl font-bold ${textColor}`}>{projectName}</h1>
          <button
            onClick={handleExportPDF}
            className={`${buttonBgColor} ${buttonTextColor} px-4 py-2 rounded-full ${hoverButtonBgColor} transition-colors flex items-center`}
          >
            <FiDownload className="mr-2" /> Export PDF
          </button>
        </header>
        
        <main className="flex-grow overflow-hidden flex">
          <div className={`${isSceneMenuVisible ? 'w-1/4' : 'w-0'} ${cardBgColor} transition-all duration-300 overflow-hidden`}>
            {isSceneMenuVisible && (
              <>
                <h2 className="text-xl font-bold mb-4">Szenen</h2>
                <span className="text-gray-400 mb-4 block text-sm">
                  Total Duration: {calculateTotalDuration(shotlist.scenes)}
                </span>
                <Droppable droppableId="scenes" type="scene">
                  {(provided) => (
                    <ul {...provided.droppableProps} ref={provided.innerRef}>
                      {shotlist.scenes.map((scene, index) => (
                        <Draggable key={scene.id} draggableId={scene.id} index={index}>
                          {(provided) => (
                            <li
                              ref={provided.innerRef}
                              {...provided.draggableProps}
                              className={`mb-2 p-2 rounded cursor-pointer flex items-center justify-between ${
                                selectedSceneId === scene.id ? `${buttonBgColor} ${buttonTextColor}` : `${cardBgColor} hover:${cardBgColor}`
                              }`}
                            >
                              <div className="flex items-center" onClick={() => handleSceneSelect(scene.id)}>
                                <div {...provided.dragHandleProps} className="mr-2">
                                  <FiMenu />
                                </div>
                                <span className="mr-2 font-bold">{scene.number}.</span>
                                <input
                                  value={scene.name}
                                  onChange={(e) => handleSceneNameChange(scene.id, e.target.value)}
                                  className="bg-transparent focus:outline-none"
                                />
                              </div>
                              <button
                                onClick={() => handleDeleteScene(scene.id)}
                                className="text-red-500 hover:text-red-700"
                              >
                                <FiTrash2 />
                              </button>
                            </li>
                          )}
                        </Draggable>
                      ))}
                      {provided.placeholder}
                    </ul>
                  )}
                </Droppable>
                <button
                  onClick={handleAddNewScene}
                  className="mt-4 w-full flex items-center justify-center p-2 rounded-full border-2 border-yellow-500 text-yellow-500 hover:bg-yellow-500 hover:text-white transition-colors duration-300"
                >
                  <FiPlus size={24} className="mr-2" /> Add New Scene
                </button>
              </>
            )}
          </div>
          
          <div className={`flex-grow p-4 overflow-y-auto transition-all duration-300 ${isSceneMenuVisible ? 'w-3/4' : 'w-full'}`}>
            <button onClick={toggleSceneMenu} className="mb-4 text-yellow-500 hover:text-yellow-700">
              {isSceneMenuVisible ? <FiChevronLeft /> : <FiChevronRight />}
            </button>
            
            {selectedScene && (
              <>
                <h2 className="text-2xl font-bold mb-4">{selectedScene.name}</h2>
                <div className="flex justify-between items-center mb-4">
                  <div className="flex space-x-2">
                    <button
                      onClick={toggleViewMode}
                      className={`${buttonBgColor} ${buttonTextColor} px-4 py-2 rounded-full ${hoverButtonBgColor} transition-colors flex items-center`}
                    >
                      {getViewModeIcon()}
                      <span className="ml-2">
                        {viewMode === 'default' ? 'Default' : viewMode === 'storyboard' ? 'Storyboard' : 'List'}
                      </span>
                    </button>
                    <button
                      onClick={() => setShowSettings(!showSettings)}
                      className={`${buttonBgColor} ${buttonTextColor} px-4 py-2 rounded-full ${hoverButtonBgColor} transition-colors flex items-center`}
                    >
                      <FiSettings className="mr-2" /> Settings
                    </button>
                  </div>
                  <span className="text-gray-400">
                    Total Duration: {calculateTotalDuration([selectedScene])}
                  </span>
                </div>
                
                {showSettings && (
                  <div className={`${cardBgColor} p-4 rounded-lg mb-4`}>
                    <h3 className="text-lg font-bold mb-2">Visible Fields</h3>
                    <div className="flex flex-wrap gap-2">
                      {['description', 'shotType', 'cameraAngle', 'cameraMove', 'lens', 'duration', 'props', 'location', 'cast'].map((field) => (
                        <label key={field} className="flex items-center">
                          <input
                            type="checkbox"
                            checked={visibleFields.includes(field)}
                            onChange={() => {
                              setVisibleFields(prev =>
                                prev.includes(field)
                                  ? prev.filter(f => f !== field)
                                  : [...prev, field]
                              );
                            }}
                            className="mr-2"
                          />
                          {field.charAt(0).toUpperCase() + field.slice(1)}
                        </label>
                      ))}
                    </div>
                  </div>
                )}
                
                {viewMode === 'default' && (
                  <div>
                    <Droppable droppableId={selectedScene.id} type="shot">
                      {(provided) => (
                        <div {...provided.droppableProps} ref={provided.innerRef}>
                          {selectedScene.shots.map((shot, index) => (
                            <Draggable key={shot.id} draggableId={shot.id} index={index}>
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                  className={`${cardBgColor} rounded-lg overflow-hidden shadow-lg mb-4 p-4`}
                                >
                                  <div className="flex items-center mb-4">
                                    <h3 className="text-xl font-bold mr-2">
                                      {renderShotNumber(selectedScene.number, shot.number)}
                                    </h3>
                                    <input
                                      value={shot.title || ''}
                                      onChange={(e) => handleShotTitleChange(selectedScene.id, shot.id, e.target.value)}
                                      placeholder="Shot title"
                                      className={`flex-grow ${inputBgColor} ${textColor} px-2 py-1 rounded`}
                                    />
                                  </div>
                                  <div className="flex">
                                    <div className="w-1/3 mr-4">
                                      <div className="w-full h-0 pb-[56.25%] relative overflow-hidden">
                                        {shot.image ? (
                                          <label className="absolute inset-0 cursor-pointer">
                                            <img 
                                              src={shot.image} 
                                              alt={`Shot ${shot.number}`} 
                                              className="w-full h-full object-cover"
                                            />
                                            <input
                                              type="file"
                                              accept="image/*"
                                              onChange={(e) => handleImageUpload(selectedScene.id, shot.id, e.target.files[0])}
                                              className="hidden"
                                            />
                                          </label>
                                        ) : (
                                          <label className="absolute inset-0 flex items-center justify-center cursor-pointer bg-gray-200">
                                            <FiUpload size={24} className="text-gray-400" />
                                            <input
                                              type="file"
                                              accept="image/*"
                                              onChange={(e) => handleImageUpload(selectedScene.id, shot.id, e.target.files[0])}
                                              className="hidden"
                                            />
                                          </label>
                                        )}
                                      </div>
                                    </div>
                                    <div className="w-2/3">
                                      <textarea
                                        value={shot.description || ''}
                                        onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { description: e.target.value })}
                                        placeholder="Description"
                                        className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded text-sm h-32 mb-4`}
                                      />
                                      <div className="grid grid-cols-2 gap-2">
                                        {visibleFields.filter(field => field !== 'description').map((field) => (
                                          <div key={field} className="relative">
                                            {['shotType', 'cameraAngle', 'cameraMove'].includes(field) ? (
                                              <select
                                                value={shot[field] || ''}
                                                onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { [field]: e.target.value })}
                                                className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded`}
                                              >
                                                <option value="">{field.charAt(0).toUpperCase() + field.slice(1)}</option>
                                                {field === 'shotType' && shotTypes.map((type) => (
                                                  <option key={type} value={type}>{type}</option>
                                                ))}
                                                {field === 'cameraAngle' && cameraAngles.map((angle) => (
                                                  <option key={angle} value={angle}>{angle}</option>
                                                ))}
                                                {field === 'cameraMove' && cameraMoves.map((move) => (
                                                  <option key={move} value={move}>{move}</option>
                                                ))}
                                              </select>
                                            ) : (
                                              <input
                                                value={shot[field] || ''}
                                                onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { [field]: e.target.value })}
                                                placeholder={field === 'duration' ? 'Duration (min)' : field.charAt(0).toUpperCase() + field.slice(1)}
                                                className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded`}
                                              />
                                            )}
                                          </div>
                                        ))}
                                      </div>
                                    </div>
                                  </div>
                                  <div className="flex justify-end mt-4">
                                    <button
                                      onClick={() => handleDuplicateShot(selectedScene.id, shot)}
                                      className="text-yellow-500 hover:text-yellow-700 mr-2"
                                    >
                                      <FiCopy />
                                    </button>
                                    <button
                                      onClick={() => handleDeleteShot(selectedScene.id, shot.id)}
                                      className="text-red-500 hover:text-red-700"
                                    >
                                      <FiTrash2 />
                                    </button>
                                  </div>
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                  </div>
                )}
                
                {viewMode === 'storyboard' && (
                  <div className={`grid ${isSceneMenuVisible ? 'grid-cols-2' : 'grid-cols-3'} gap-4`}>
                    {selectedScene.shots.map((shot, index) => (
                      <div key={shot.id} className={`${cardBgColor} rounded-lg overflow-hidden shadow-lg`}>
                        <div className="p-4">
                          <div className="flex justify-between items-center mb-2">
                            <h3 className="text-xl font-bold">
                              {renderShotNumber(selectedScene.number, shot.number)}
                            </h3>
                            <div className="flex space-x-2">
                              <button
                                onClick={() => handleDuplicateShot(selectedScene.id, shot)}
                                className="text-yellow-500 hover:text-yellow-700"
                              >
                                <FiCopy />
                              </button>
                              <button
                                onClick={() => handleDeleteShot(selectedScene.id, shot.id)}
                                className="text-red-500 hover:text-red-700"
                              >
                                <FiTrash2 />
                              </button>
                            </div>
                          </div>
                          <input
                            value={shot.title || ''}
                            onChange={(e) => handleShotTitleChange(selectedScene.id, shot.id, e.target.value)}
                            placeholder="Shot title"
                            className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded mb-2`}
                          />
                          <div className="mb-2">
                            {shot.image ? (
                              <div className="w-full h-0 pb-[56.25%] relative">
                                <img 
                                  src={shot.image} 
                                  alt={`Shot ${shot.number}`} 
                                  className="absolute inset-0 w-full h-full object-contain"
                                />
                              </div>
                            ) : (
                              <div className={`w-full h-0 pb-[56.25%] ${inputBgColor} rounded relative`}>
                                <label className="absolute inset-0 flex items-center justify-center cursor-pointer">
                                  <FiUpload size={24} className="text-gray-400" />
                                  <input
                                    type="file"
                                    accept="image/*"
                                    onChange={(e) => handleImageUpload(selectedScene.id, shot.id, e.target.files[0])}
                                    className="hidden"
                                  />
                                </label>
                              </div>
                            )}
                          </div>
                          <div className="space-y-2">
                            <div className="relative">
                              <textarea
                                value={shot.description || ''}
                                onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { description: e.target.value })}
                                placeholder="Description"
                                className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded text-sm h-24`}
                              />
                            </div>
                            {visibleFields.filter(field => field !== 'description').map((field) => (
                              <div key={field} className="relative">
                                <input
                                  value={shot[field] || ''}
                                  onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { [field]: e.target.value })}
                                  placeholder={field === 'duration' ? 'Duration (min)' : field.charAt(0).toUpperCase() + field.slice(1)}
                                  className={`w-full ${inputBgColor} ${textColor} px-2 py-1 rounded text-sm`}
                                />
                              </div>
                            ))}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
                
                {viewMode === 'list' && (
                  <Droppable droppableId={selectedScene.id} type="shot">
                    {(provided) => (
                      <div {...provided.droppableProps} ref={provided.innerRef}>
                        <table className="w-full">
                          <thead>
                            <tr className="border-b border-gray-700">
                              <th className="text-left p-2">Shot</th>
                              <th className="text-left p-2">Title</th>
                              {visibleFields.map((field) => (
                                <th key={field} className="text-left p-2">
                                  {field === 'duration' ? 'Duration (min)' : field.charAt(0).toUpperCase() + field.slice(1)}
                                </th>
                              ))}
                              <th className="text-left p-2">Actions</th>
                            </tr>
                          </thead>
                          <tbody>
                            {selectedScene.shots.map((shot, index) => (
                              <Draggable key={shot.id} draggableId={shot.id} index={index}>
                                {(provided) => (
                                  <tr
                                    ref={provided.innerRef}
                                    {...provided.draggableProps}
                                    {...provided.dragHandleProps}
                                    className="border-b border-gray-800 hover:bg-gray-800"
                                  >
                                    <td className="p-2 font-bold">{renderShotNumber(selectedScene.number, shot.number)}</td>
                                    <td className="p-2">
                                      <input
                                        value={shot.title || ''}
                                        onChange={(e) => handleShotTitleChange(selectedScene.id, shot.id, e.target.value)}
                                        className="bg-transparent focus:outline-none focus:ring-1 focus:ring-yellow-500 w-full"
                                        placeholder="Shot title"
                                      />
                                    </td>
                                    {visibleFields.map((field) => (
                                      <td key={field} className="p-2">
                                        {['shotType', 'cameraAngle', 'cameraMove'].includes(field) ? (
                                          <select
                                            value={shot[field] || ''}
                                            onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { [field]: e.target.value })}
                                            className="bg-transparent focus:outline-none focus:ring-1 focus:ring-yellow-500 w-full"
                                          >
                                            <option value="">Select {field}</option>
                                            {field === 'shotType' && shotTypes.map((type) => (
                                              <option key={type} value={type}>{type}</option>
                                            ))}
                                            {field === 'cameraAngle' && cameraAngles.map((angle) => (
                                              <option key={angle} value={angle}>{angle}</option>
                                            ))}
                                            {field === 'cameraMove' && cameraMoves.map((move) => (
                                              <option key={move} value={move}>{move}</option>
                                            ))}
                                          </select>
                                        ) : (
                                          <input
                                            value={shot[field] || ''}
                                            onChange={(e) => handleUpdateShot(selectedScene.id, shot.id, { [field]: e.target.value })}
                                            className="bg-transparent focus:outline-none focus:ring-1 focus:ring-yellow-500 w-full"
                                            placeholder={field === 'duration' ? 'Duration (min)' : field}
                                          />
                                        )}
                                      </td>
                                    ))}
                                    <td className="p-2">
                                      <div className="flex items-center">
                                        <button
                                          onClick={() => handleDuplicateShot(selectedScene.id, shot)}
                                          className="text-yellow-500 hover:text-yellow-700 mr-2"
                                        >
                                          <FiCopy />
                                        </button>
                                        <button
                                          onClick={() => handleDeleteShot(selectedScene.id, shot.id)}
                                          className="text-red-500 hover:text-red-700"
                                        >
                                          <FiTrash2 />
                                        </button>
                                      </div>
                                    </td>
                                  </tr>
                                )}
                              </Draggable>
                            ))}
                          </tbody>
                        </table>
                        {provided.placeholder}
                      </div>
                    )}
                  </Droppable>
                )}
                
                <button
                  onClick={handleAddNewShot}
                  className="mt-4 w-full flex items-center justify-center p-2 rounded-full border-2 border-yellow-500 text-yellow-500 hover:bg-yellow-500 hover:text-white transition-colors duration-300"
                >
                  <FiPlus size={24} />
                </button>
              </>
            )}
          </div>
        </main>

        {showPDFExportModal && (
          <PDFExportModal
            shotlist={shotlist}
            projectName={projectName}
            visibleFields={visibleFields}
            onClose={() => setShowPDFExportModal(false)}
            cardBgColor={cardBgColor}
            textColor={textColor}
            inputBgColor={inputBgColor}
            buttonBgColor={buttonBgColor}
            buttonTextColor={buttonTextColor}
            director={director}
            dp={dp}
            production={production}
            producer={producer}
            camOp={camOp}
            shootingDate={shootingDate}
          />
        )}
      </div>
    </DragDropContext>
  );
};

export default ShotlistTable;
                                