import React, { useState, useEffect } from 'react';
import { auth } from '../utils/firebase';
import { onAuthStateChanged } from 'firebase/auth';
import Login from './Login';
import Register from './Register';
import Logout from './Logout';
import { FiFilm } from 'react-icons/fi';

function AuthWrapper({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showRegister, setShowRegister] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  if (loading) {
    return <div className="min-h-screen bg-gray-900 text-gray-100 flex items-center justify-center">Laden...</div>;
  }

  if (!user) {
    return (
      <div className="min-h-screen bg-gray-900 text-gray-100 flex flex-col items-center justify-center">
        <div className="bg-gray-800 p-8 rounded-lg shadow-xl w-96">
          <h1 className="text-2xl font-bold flex items-center mb-6">
            <FiFilm className="mr-2 text-yellow-400" /> Directors Deck
          </h1>
          {showRegister ? <Register /> : <Login />}
          <button 
            onClick={() => setShowRegister(!showRegister)}
            className="mt-4 text-sm text-gray-400 hover:text-gray-300"
          >
            {showRegister ? "Bereits registriert? Anmelden" : "Noch kein Konto? Registrieren"}
          </button>
        </div>
      </div>
    );
  }

  return (
    <div>
      {children}
    </div>
  );
}

export default AuthWrapper;